export default {
  name: 'Todo',
  data(){
    return{
      newTask:'',
      tasks:[]
    }
  },
  methods:{
    deleteTask(index){
      this.$q.dialog({
        title: 'Delete',
        message: 'Confirm Delete?'
      }).onOk(() => {
        // console.log('OK')
        this.tasks.splice(index,1);

        // inside of a Vue component
        this.$q.notify.setDefaults({
          position: 'top-right',
          timeout: 2500,
          textColor: 'white',
          color:'primary',
          actions: [{ icon: 'close', color: 'white' }]
        })
        this.$q.notify('Task Deleted!')

      }).onCancel(() => {
        // console.log('Cancel')
      }).onDismiss(() => {
        // console.log('I am triggered on both OK and Cancel')
      })
    },
    addTask(){

      if(this.newTask !=''){

        this.tasks.push({
          title:this.newTask,
          done: false
        })
        this.newTask=''

      }else{
        this.$q.notify('Can not add empty task!')

      }

    }

  }
}
